body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(0, 111, 130);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contenedor {
  max-width: 350px;
  padding: 10px 50px;
  width: 100%;
  margin: 0 auto;
 text-align: center;
 background-color: rgb(36, 35, 35);
 border-radius: 5px;
 box-shadow:
  0 2.8px 2.2px rgba(0, 0, 0, 0.001),
  0 6.7px 5.3px rgba(0, 0, 0, 0.003),
  0 12.5px 10px rgba(0, 0, 0, 0.006),
  0 22.3px 17.9px rgba(0, 0, 0, 0.009),
  0 41.8px 33.4px rgba(0, 0, 0, 0.013),
  0 100px 80px rgba(0, 0, 0, 0.02)
;
}
.cita {
  
 min-height: 250px;
 width: 90%;
 margin: 40px auto;
}
input {
  margin-top: 20px !important;
}
h1 {
  margin-top: 50px;
  color: white;
}

.cada {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
 
}
p {
  margin: 0px;
}

.boton {

  background-color: rgb(0, 56, 130) !important;
  color: white !important;
  border-radius: 20px;
  border:0px;
  text-align: center;
  cursor: pointer;
}
.in {
  border-radius: 20px;
  width: 400px;
}

span {
  cursor: pointer;
}

.rel {
  position: relative;
  width: 100%;
}

.cuenta {
  position: absolute;
  right: 15px;
  top : 50%;
  color: rgb(13, 145, 206);
  
}